<template>
  <div class="wrap">
    <div class="content">
      <img :src="imgSrc" :alt="title">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: String
  },
  data() {
    return {
      imgSrc: `./static/img/${this.title}`
    };
  }
};
</script>

<style scoped>
.wrap {
  position: absolute;
  top: 70px;
  bottom: 0;
}
.content {
  height: 100%;
  text-align: left;
  overflow: auto;
}
</style>


