<template>
  <div class="post" @click="$emit('click')">
    <div>{{ title }}</div>
    <span class="time">{{ time }}</span>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ""
    },
    time: {
      type: String,
      default: ""
    }
  }
};
</script>

<style scoped>
.post {
  width: 60%;
  margin: 20px auto;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 2px #ccc;
  cursor: pointer;
}
.post:hover {
  box-shadow: 0 0 20px #ccc;
}
.time {
  font-size: 12px;
}
</style>


