<template>
  <div class="cover">
    <div class="blog-title vertical-align">
      <div>{{ name }}</div>
      <AnimItem>
        <BaseIcon v-if="showArrow" type="angle-double-right" @click="login"></BaseIcon>
      </AnimItem>
    </div>
  </div>
</template>

<script>
import BaseIcon from "@/components/Icon/BaseIcon";
import AnimItem from "@/components/Anim/AnimItem";

export default {
  components: { BaseIcon, AnimItem },
  data() {
    return {
      name: "LoveBlog",
      showArrow: true
    };
  },
  methods: {
    login() {
      this.$router.push("login");
    }
  }
};
</script>

<style scoped>
.cover {
  height: 100%;
  font-size: 60px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}
.vertical-align {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
}
</style>


