<template>
  <PostBase :title="title" :time="time" @click="enterPost"></PostBase>
</template>

<script>
import PostBase from "@/components/Blog/Post/PostBase";
export default {
  components: { PostBase },
  props: {
    title: {
      type: String,
      default: ""
    },
    time: {
      type: String,
      default: ""
    },
    name: {
      type: String,
      default: ""
    },
    directUrl: {
      type: String,
      default: ""
    }
  },
  methods: {
    enterPost() {
      let url =
        this.directUrl == ""
          ? `/user/${this.$route.params.id}/blog/postImage/${this.name}`
          : this.directUrl;
      this.$router.push(url);
    }
  }
};
</script>

<style scoped>
</style>


